<template>
    <div class="container">
        <div class="main__pictures" v-if="pics">
            <div class="main__pictures-item" v-for="pic in pics" key:pic.id>
                <p>Id: {{ pic.id }}</p>
                <router-link class="lin" :to="/posts/ + pic.id" ><span>НАЖМИ:</span>   {{pic.title }}</router-link>
                <p class="title">Title: {{ pic.title }}</p>
                <img class="img" :src="pic.url" alt="">
                <img :src="pic.thumbnailUrl" alt="">
            </div>           
        </div> 
        <div class="main__section" v-else> Waiting........</div>
    </div>
</template>

<script>
    export default {
        data(){
        return {
            pics: null
            }
        },
        created() {
            this.getPics()
        },
        methods:{
            async getPics(){
            let response = await fetch ('https://jsonplaceholder.typicode.com/photos')
            let data = await response.json()
            this.pics = data
            }
        }
    }
</script>

























